.info__wrapper {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.info__image__wrapper {
  width: 230px;
  height: 230px;
  border: 4px solid #252525;
  border-radius: 50%;
  overflow: hidden;
}

.info__image {
  width: 100%;
  height: 100%;
}

.info__heading__text {
  text-transform: uppercase;
  text-align: center;
  color: #ffb400;
  max-width: 550px;
  margin: 18px 0 13px;
  font-size: 29px;
  line-height: 39px;
}

.info__heading__split {
  color: #fff;
}

.info__details__text {
  color: #fff;
  text-align: center;
  max-width: 550px;
  margin: 10px 0 23px;
  font-size: 14px;
  line-height: 28px;
}

.primary__btn {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #252525;
  border-radius: 3px;
  padding: 8px 15px;
  font-size: 16px;
  font-weight: 500;
  transition: all .5s;
  position: relative;
}

.primary__btn:first-letter {
  color: #111;
}

.primary__btn:before {
  content: "";
  width: 20%;
  height: 100%;
  z-index: -1;
  background-color: #ffb400;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.primary__btn:hover:before {
  width: 100%;
}

.primary__btn:hover {
  color: #111;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Rubik, sans-serif;
}

body {
  background-color: #111;
}

.container {
  max-width: 1370px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

